* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/ticket-tracker/static/media/hd-wallpaper-g8ccec26d8_1280.b1cc50c1.jpg);
  background-repeat: repeat;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app{padding:2rem;display:flex;flex-direction:column;align-items:center;color:#000;font-family:"Edu TAS Beginner",cursive;grid-gap:1rem;gap:1rem}@media screen and (min-width: 768px){.app{grid-gap:1.5rem;gap:1.5rem}}.app__ticket-container{display:flex;flex-direction:row;align-items:center;justify-content:space-evenly;flex-wrap:wrap;max-width:80rem;grid-gap:1rem;gap:1rem}@media screen and (min-width: 768px){.app__ticket-container{grid-gap:1.5rem;gap:1.5rem}}
.header{display:flex;align-items:center;justify-content:center;height:5rem;width:15rem;background-color:#fff;font-size:2.5rem;margin-top:.3rem;position:relative;box-shadow:.025rem .025rem .25rem}@media screen and (min-width: 768px){.header{width:17rem}}.header:hover{-webkit-transform:rotate(3deg);transform:rotate(3deg);-webkit-transform-origin:top center;transform-origin:top center;transition:-webkit-transform .2s;transition:transform .2s;transition:transform .2s, -webkit-transform .2s}.header__pin{position:absolute;width:.5rem;height:.5rem;top:.2rem;border-radius:2rem;background-color:#000;box-shadow:.1rem .1rem .2rem #000}.header__heading{font-size:2.5rem}
.filters{display:flex;flex-direction:column;align-items:center;width:18rem;background-color:#fff;margin-top:.3rem;position:relative;box-shadow:.025rem .025rem .25rem;padding:.5rem;padding-bottom:.8rem}@media screen and (min-width: 768px){.filters{width:22rem;padding-bottom:1rem}}.filters:hover{-webkit-transform:rotate(3deg);transform:rotate(3deg);-webkit-transform-origin:top center;transform-origin:top center;transition:-webkit-transform .2s;transition:transform .2s;transition:transform .2s, -webkit-transform .2s}.filters__pin{position:absolute;width:.5rem;height:.5rem;top:.2rem;border-radius:2rem;background-color:#000;box-shadow:.1rem .1rem .2rem #000}.filters__heading{margin-top:.5rem;font-size:1.5rem}@media screen and (min-width: 768px){.filters__heading{font-size:1.8rem;margin-bottom:.2rem}}.filters__main{display:flex;flex-direction:column;grid-gap:.5rem;gap:.5rem}@media screen and (min-width: 768px){.filters__main{grid-gap:.8rem;gap:.8rem;font-size:1.2rem}}.filters__label{margin-right:.2rem}.filters__select-option{font-family:"Edu TAS Beginner",cursive}
.employee{display:flex;flex-direction:column;align-items:center;width:14rem;min-height:12rem;margin-top:.3rem;position:relative;box-shadow:.025rem .025rem .25rem;padding:.5rem}.employee:hover{-webkit-transform:rotate(-3deg);transform:rotate(-3deg);-webkit-transform-origin:top center;transform-origin:top center;transition:-webkit-transform .2s;transition:transform .2s;transition:transform .2s, -webkit-transform .2s}.employee__pin{position:absolute;width:.5rem;height:.5rem;top:.2rem;border-radius:2rem;background-color:#000;box-shadow:.1rem .1rem .2rem #000}.employee__details{margin-top:.5rem}
.counter{font-family:"Roboto",sans-serif;margin-top:1.8rem;display:flex;flex-direction:column;align-items:center;width:100%}.counter__count-container{margin-top:1.5rem;display:flex;grid-gap:2rem;gap:2rem;font-size:1.5rem}.counter__button{border:none;background-color:inherit;font-size:1.5rem}.counter__button:hover{color:#fff}
